.wp-block-tl-text-box {

	&.is-style-squared {
		border-radius:0px;
	}
	&.is-style-rounded {
		border-radius:10px;
	}

	&.has-shadow {
		box-shadow: 0 0 20px rgba(0, 0, 0, 0.3);
		&.has-shadow-opacity-10{
			box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
		}
		&.has-shadow-opacity-20{
			box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
		}
		&.has-shadow-opacity-30{
			box-shadow: 0 0 20px rgba(0, 0, 0, 0.3);
		}
		&.has-shadow-opacity-40{
			box-shadow: 0 0 20px rgba(0, 0, 0, 0.4);
		}
	}
	&.text-box-align-left {
		text-align: left;
	}
	&.text-box-align-center {
		text-align: center;
	}
	&.text-box-align-right {
		text-align: right;
	}
}
